body {
  background: #f5f5f5; }

.news_list_module .date_tit {
  padding-left: 15px;
  font-size: 3em;
  color: #b2bac2;
  font-weight: 500;
  line-height: 2; }

.news_list_module .news_list li a {
  display: flex;
  display: -webkit-flex;
  display: -moz-flex;
  margin-bottom: 15px;
  padding: 10px;
  background: #fff; }
  .news_list_module .news_list li a .new_pic {
    display: block;
    margin-left: 10px;
    width: 6em;
    height: 6em; }
  .news_list_module .news_list li a .new_tit {
    -webkit-flex: 1;
    -moz-flex: 1;
    flex: 1;
    font-size: 2em;
    color: #333; }

.news_list_module .more_btn {
  display: block;
  font-size: 1.5em;
  padding: 1em 0;
  width: 100%;
  border: 0;
  color: #fff;
  background-color: #252e39;
  border: none;
  margin: 10px 0;
  border-radius: .5em;
  outline: none;
  cursor: pointer;
  text-align: center; }



