.details_module .head_img {
  position: relative; }
  .details_module .head_img img {
    width: 100%;
    vertical-align: top; }
  .details_module .head_img .tit {
    font-size: 4em;
    color: #fff;
    position: absolute;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    width: 90%;
    text-align: center; }
  .details_module .head_img .source {
    color: #e2e2e2;
    bottom: 15px;
    right: 15px;
    position: absolute;
    font-size: 2em; }

.details_module a {
  font-size: 2em;
  color: #333;
  text-decoration: underline; }



